.ProgressBlocks {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 1em;
}
.ProgressBlock {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 40px;
  margin-left: 0.5em;
  margin-right: 0.5em;
  background-color: #fff;
  color: #808080;
  font-weight: bold;
  line-height: 40px;
  text-align: center;
  -webkit-transition-property: color, background-color, box-shadow;
  transition-property: color, background-color, box-shadow;
  -webkit-transition-duration: 0.2s;
          transition-duration: 0.2s;
}
.ProgressBlock:first-child {
  margin-left: 0;
}
.ProgressBlock:last-child {
  margin-right: 0;
}
.ProgressBlock.is-active {
  color: #fff;
  background-color: #ffb200;
  box-shadow: 0 0 10px #ffb200;
}
.ProgressBlock.is-done {
  color: transparent;
  background-color: #76b900;
  background-image: url("../assets/checkmark.svg");
  background-position: center;
  background-repeat: no-repeat;
}
.ProgressBlock.is-done.is-active {
  color: #fff;
  background-image: none;
  box-shadow: 0 0 10px #76b900;
}


